.block-photo {
    margin-bottom: clampFluid(70, 160);

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__btn {
        width: clampFluid(351);
        margin-bottom: clampFluid(60);

        @media (max-width: 1280px) {
            display: none;
        }
    }

    &__title {
        @include h2;
        font-size: clampFluid(32, 200, 320, 1920);
        line-height: clampFluid(38, 240, 320, 1920);
        text-align: center;
        margin-bottom: clampFluid(20, 10);
        color: white;
    }

    &__img-container {
        &:nth-child(1n) {
            grid-column: 1/2;
            grid-row: 1/2;

            @media (max-width: 600px) {
                grid-column: 1/2;
                grid-row: 1/2;
            }
        }

        &:nth-child(2n) {
            grid-column: 2/3;
            grid-row: 1/3;
            @media (max-width: 600px) {
                grid-column: 2/3;
                grid-row: 1/3;
          
            }
        }

        &:nth-child(3n) {
            grid-row: 1/2;
            grid-column: 3/4;
            @media (max-width: 600px) {

                grid-column: 1/2;
                grid-row: 4/5;
            }
        }

        &:nth-child(4n) {
            grid-row: 2/4;
            grid-column: 1/2;
            @media (max-width: 600px) {
                grid-column: 1/2;
                grid-row: 2/4;
            }
        }

        &:nth-child(5n) {
            grid-row: 3/4;
            grid-column: 2/3;
            @media (max-width: 600px) {

                grid-column: 2/3;
                grid-row: 3/4   ;
            }
        }

        &:nth-child(6n) {
            grid-row: 2/4;
            grid-column: 3/4;
            @media (max-width: 600px) {
                grid-column: 1/2;
                grid-row: 5/7;
            }
        }

        &:nth-child(7n) {
            grid-column: 1/2;
            grid-row: 4/5;
            @media (max-width: 600px) {
                grid-column: 2/3;
                grid-row: 4/5;
            }
        }

        &:nth-child(8n) {
            grid-column: 2/3;
            grid-row: 4/6;
            @media (max-width: 600px) {
                grid-column: 1/2;
                grid-row: 7/9;
            }
        }

        &:nth-child(9n) {
            grid-row: 4/5;
            grid-column: 3/4;
            @media (max-width: 600px) {
                grid-column: 2/3;
                grid-row: 7/8;
            }
        }

        &:nth-child(10n) {
            grid-row: 5/7;
            grid-column: 1/2;
            @media (max-width: 600px) {
  
                grid-column: 2/3;
                grid-row: 5/7;
            }
        }

        &:nth-child(11n) {
            grid-row: 6/7;
            grid-column: 2/3;
            @media (max-width: 600px) {
                grid-column: 1/2;
                grid-row: 9/10;
            }
        }

        &:nth-child(12n) {
            grid-row: 5/7;
            grid-column: 3/4;
            @media (max-width: 600px) {
                grid-column: 2/3;
                grid-row: 8/10;
            }
        }
    }

    &__gallery-wrapper {
        display: flex;
        flex-direction: column;
        gap: clampFluid(10, 30);
        margin-bottom: clampFluid(40);
    }

    &__gallery {
        width: 100%;
        display: grid;
        gap: clampFluid(10, 30);
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: clampFluid(180, 530, 600, 1920) clampFluid(130, 210, 600, 1920) clampFluid(180, 530, 600, 1920) clampFluid(180, 530, 600, 1920) clampFluid(130, 210, 600, 1920) clampFluid(180, 530, 600, 1920);


        @media (max-width: 600px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(3, clampFluid(140, 285, 320, 600) clampFluid(45, 90, 320, 600) clampFluid(140, 285, 320, 600));
        }
    }

    &__seeAll {
        text-align: center;
        @include h5;
        color: $green;
        transition: $tr-f;

        @include hover {
            color: $red;
        }
    }
}