// Fonts
@function calcFluid($f-min, $f-max, $w-min: 320, $w-max: 1920) {
	$k: (
		$f-max - $f-min)/($w-max - $w-min
	);
$b: $f-min - $k * $w-min;

$b: $b * 1px;

@return calc(#{$k} * 100vw + #{$b});
}

